<script>
import * as XLSX from 'xlsx';
import jsPDF from 'jspdf';
import 'jspdf-autotable';


export default {
  name: "AsignarAulaAsiento",

  data() {
    return {
      openDialog: false,
      items:[
        {
          nombre: 'MODALIDAD',
          tipo: 'GENERAL - EXCEPCIONAL (1 A 4 DE SECUNDARIA)',
          label: 'Distribuir aulas y asientos aleatoriamente',
          action:()=> this.GenerarAsignacionAulaAsiento()
        },
        {
          nombre: 'MODALIDAD',
          tipo: 'EXONERADOS',
          label: 'Distribuir aulas y asientos aleatoriamente',
          action:()=>this.GenerarAsignacionAulaAsientoExonerados()
        },
      ],
      amount: null,
      isLoading: false,
      valSelect:'',
      value: '',
      visibleSelect:false,
      ItemList: null,

    };
  },

  computed: {
    ListAsignarAulaAsiento() {
      return this.$store.getters["distribucionAulaAsiento/get_asignarAulaAsiento"];
    },

    domain_object() {
      return this.$store.getters["tools/get_domain"];
    },

    filter_data(){
      return this.$store.getters["distribucionAulaAsiento/get_filter_data"];
    },

    listAulas(){
      return this.$store.getters["distribucionAulaAsiento/get_listAulas"];
    },

    listEscuelas(){
      return this.$store.getters["distribucionAulaAsiento/get_listEscuelas"];
    },

    get_data_proceso_admision(){
      return this.$store.getters["distribucionAulaAsiento/get_data_proceso_admision"];
    }

  },

  methods:{

    async initialData(){
      this.valSelect = 'Todos'
      await this.$store.dispatch("distribucionAulaAsiento/get_getProcesoAdmision");
    },

    formatDate(date){
      const days = ['Domingo', 'Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes', 'Sbado'];
      const months = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'];

      const dateArray = date.split('-');
      const newDate = new Date(dateArray[0], dateArray[1] - 1, dateArray[2]);
      const day = days[newDate.getDay()];
      const month = months[newDate.getMonth()];
      return `${day} ${newDate.getDate()} de ${month} ${newDate.getFullYear()}`.toUpperCase();
    },

    formatDomainName(name) {
      const match = name.match(/(\d{4})-(\d)/);
      if (match) {
        const romanNumerals = ['I', 'II'];
        const phase = parseInt(match[2], 10);
        return `${match[1]}-${romanNumerals[phase - 1]}`.toUpperCase();
      }
      return '';
    },

    formatHour(hour) {
      const [hours, minutes] = hour.split(':');
      const ampm = hours >= 12 ? 'PM' : 'AM';
      const newHours = hours % 12 || 12;
      const formattedHours = newHours < 10 ? `0${newHours}` : newHours;
      return `${formattedHours}:${minutes} ${ampm}`;
    },

    showLoading() {
      return this.$vs.loading({
        ref: this.$refs.box0,
        type: 'corners',
        text: 'Loading...'
      });
    },

    showNotification(status) {
      this.$vs.notification({
        title: status ? "xito " : "Error ",
        text: status ? "Se distribuyeron aulas y asientos aleatoriamente" : "Error al distribuir aulas y asientos",
        color: status ? "success" : "danger",
      });
    },

    async GenerarAsignacionAulaAsiento() {
      const loading = this.showLoading();
      const status = await this.$store.dispatch("distribucionAulaAsiento/getListAsignarAulaAsiento");

      this.showNotification(status);
      loading.close();
    },

    async GenerarAsignacionAulaAsientoExonerados() {
      const loading = this.showLoading();
      const status = await this.$store.dispatch("distribucionAulaAsiento/getListAsignarAulaAsientoExonerados");

      this.showNotification(status);
      loading.close();
    },

    async InsertarAsiento() {
      if (!this.ListAsignarAulaAsiento) return;
      this.isLoading = true;
      const {status , data} = await this.$store.dispatch("distribucionAulaAsiento/insertarAsientos", this.ListAsignarAulaAsiento);
      if (status) {
        this.$vs.notification({
          title: "xito",
          text: data.message,
          color: "success",
        });
      }
      this.isLoading = false;
      this.openDialog = false;
    },

    // por el momento : createExcel queda desabilitado
    async createExcel() {
      const ws = XLSX.utils.json_to_sheet(this.ListAsignarAulaAsiento);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "Sheet1");
      XLSX.writeFile(wb, "distribucin_aula_asiento.xlsx");
    },

    async descargarPDF() {
      const doc = new jsPDF();
      const columns = [
        { header: 'DNI', dataKey: 'dni' },
        { header: 'POSTULANTE', dataKey: 'nombre_completo' },
        { header: 'AULA', dataKey: 'aula' },
        { header: 'ASIENTO', dataKey: 'asiento' }
      ];

      const rows = this.ListAsignarAulaAsiento.map(row => ({
        dni: row.dni,
        nombre_completo: row.nombre_completo,
        aula: row.aula,
        asiento: row.asiento
      }));

      const pageWidth = doc.internal.pageSize.getWidth();
      const titles = [
        { text: `PROCESO DE ADMISIN ${this.formatDomainName(this.domain_object.name)} FASE`, size: 14, color: 40 },
        { text: 'DISTRIBUCIN DE AULAS', size: 16, color: 40 },
        { text: `${this.get_data_proceso_admision[0].nombre.toUpperCase()}`, size: 12, color: 40 },
        { text: `${this.formatDate(this.get_data_proceso_admision[0].fechaExamen)}`, size: 16, color: 'red' },
        { text: `INGRESO:${this.formatHour(this.get_data_proceso_admision[0].hora_entrada)}        CIERRE:${this.formatHour(this.get_data_proceso_admision[0].hora_cierre)}`, size: 14, color: 40 }
      ];

      const addHeader = (doc, y) => {
        doc.setFont('helvetica', 'bold');
        let currentY = y - 3;
        titles.forEach(title => {
          doc.setFontSize(title.size);
          doc.setTextColor(title.color);
          const textWidth = doc.getTextWidth(title.text);
          const x = (pageWidth - textWidth) / 2;
          doc.text(title.text, x, currentY);
          currentY += 7;
        });
        return currentY;
      };

      const maxRowsPerPage = 40;

      // Agrupar por aula
      const aulas = {};
      rows.forEach(row => {
        if (!aulas[row.aula]) {
          aulas[row.aula] = [];
        }
        aulas[row.aula].push(row);
      });

      // Recorrer aulas y generar pginas
      Object.keys(aulas).forEach((aula, index) => {
        if (index > 0) doc.addPage();

        let currentY = addHeader(doc, 10);
        const aulaRows = aulas[aula];

        for (let i = 0; i < aulaRows.length; i += maxRowsPerPage) {
          if (i > 0) {
            doc.addPage();
            currentY = addHeader(doc, 10);
          }

          const rowsForPage = aulaRows.slice(i, i + maxRowsPerPage);

          doc.autoTable({
            head: [columns.map(col => col.header)],
            body: rowsForPage.map(row => columns.map(col => row[col.dataKey] || '')),
            theme: 'grid',
            startY: currentY - 6,
            margin: { right: 15, bottom: 5, left: 15 },
            styles: { fontSize: 11, halign: 'center', lineColor: [0, 0, 0] },
            bodyStyles: { fontSize: 13, cellPadding: { top: 0.8, left: 0.8, right: 0.8 }, textColor: [0, 0, 0] },
            headStyles: { fillColor: [0, 115, 54], cellPadding: { top: 0.8, left: 1, right: 1 }, lineColor: [0, 0, 0], lineWidth: 0.2 },
            columnStyles: {
              0: { halign: 'right', cellWidth: 25 },
              1: { halign: 'left',  overflow: 'ellipsize' },
              2: { cellWidth: 13 },
              3: { cellWidth: 19 }
            }
          });
        }
      });

      doc.save('distribucion_aula_asiento.pdf');
    },

    searchData(){
      if(!this.value) return
      this.$store.commit("distribucionAulaAsiento/set_textFilter", this.value);
      return  this.$store.getters["distribucionAulaAsiento/get_filter_data"]
    },

    listSelectItem(item){
      this.value = ''
      if(item === 'Escuela') return this.ItemList = this.listEscuelas;
      return this.ItemList =  this.listAulas;
    }

  },

  watch: {
    ListAsignarAulaAsiento() {
      if (this.ListAsignarAulaAsiento) this.amount = this.ListAsignarAulaAsiento.length;
    },

    filter_data(){
      if (this.filter_data) this.amount = this.filter_data.length;
    },

    valSelect(val){
      if (val === 'Todos') {
        this.$store.commit("distribucionAulaAsiento/set_textFilter", '');
        this.visibleSelect = false
        return this.$store.getters["distribucionAulaAsiento/get_filter_data"];
      }
      this.listSelectItem(val);
      return this.visibleSelect = true;
    },

  },

  async mounted() {
    await this.initialData()
  },

  // al salir del modulo limpiar el store
  beforeDestroy() {
    this.$store.commit("distribucionAulaAsiento/set_asignarAulaAsiento", null);
  }

}
</script>

<template>
  <div>
     <h1 class="text-xl font-medium cursor-pointer">Distribucin de aulas de los postulantes</h1>

    <div class="flex mt-2  justify-evenly  items-center " >
      <vs-card v-for="(item, index) in items" :key="index" >
        <template #title>
          <h3 class="text-xl font-bold " >{{item.nombre}}</h3>
          <span>{{item.tipo}}</span>
        </template>
        <template #text>
          <vs-button success @click="item.action" >{{item.label}}</vs-button>
        </template>
      </vs-card>
    </div>

    <div class="flex mt-5" >
<!--      <vs-button :disabled="!ListAsignarAulaAsiento"  success gradient @click="createExcel" >Exportar Excel</vs-button>-->
      <vs-button :disabled="!ListAsignarAulaAsiento"  primary gradient @click="descargarPDF" >Descargar PDF</vs-button>
      <vs-button :disabled="!ListAsignarAulaAsiento"  @click="openDialog = true" >Importar asientos</vs-button>
    </div>

    <div class="mt-5" >
      <h2 class="text-lg font-bold"># Cantidad de postulantes: {{ filter_data ? amount : 0 }}</h2>
      <vs-table striped class="cursor-pointer">

        <template #header>

          <div>
            <h2 class="font-bold">#  Filtros</h2>
            <div class=" flex gap-2">
              <vs-radio :disabled="!filter_data" v-model="valSelect" val="Todos">Todos</vs-radio>
              <vs-radio :disabled="!filter_data" v-model="valSelect" val="Escuela">Escuela</vs-radio>
              <vs-radio :disabled="!filter_data" v-model="valSelect" val="Aula">Aula</vs-radio>
            </div>
          </div>

          <div v-if="visibleSelect" class="flex gap-2">
            <vs-select block :placeholder="'Select ' + valSelect" v-model="value" :id="valSelect" @change="searchData" >
              <vs-option v-for="(item, index) in ItemList " :key="index" :label="item" :value="item" >
                {{item}}
              </vs-option>
            </vs-select>
          </div>
        </template>

        <template #thead>
          <vs-tr>
            <vs-th style="width:50px" >ID</vs-th>
            <vs-th>Apellido y Nombre</vs-th>
            <vs-th>N DNI</vs-th>
            <vs-th>Discapacidad</vs-th>
            <vs-th>CodModa</vs-th>
            <vs-th>Modalidad</vs-th>
            <vs-th>CodEsc</vs-th>
            <vs-th>Escuela</vs-th>
            <vs-th>Area</vs-th>
            <vs-th>Aula</vs-th>
            <vs-th>Asiento</vs-th>
          </vs-tr>
        </template>
        <template #tbody>
          <vs-tr
              :key="i"
              v-for="(tr, i) in filter_data"
              :data="tr"
          >
            <vs-td>
              {{ tr.id }}
            </vs-td>
            <vs-td>
              {{ tr.nombre_completo }}
            </vs-td>
            <vs-td>
              {{ tr.dni }}
            </vs-td>
            <vs-td>
              {{ tr.limitacion_permanente }}
            </vs-td>
            <vs-td>
              {{ tr.codModa }}
            </vs-td>
            <vs-td>
              {{ tr.modalidad }}
            </vs-td>
            <vs-td>
              {{ tr.codEsc }}
            </vs-td>
            <vs-td>
              {{ tr.escuela }}
            </vs-td>
            <vs-td>
              {{ tr.area }}
            </vs-td>
            <vs-td>
              {{ tr.aula }}
            </vs-td>
            <vs-td>
              {{ tr.asiento }}
            </vs-td>

          </vs-tr>
        </template>

        <template #notFound>
          No se encontraron datos. 
        </template>
      </vs-table>

    </div>

    <div>
      <vs-dialog :loading="isLoading"  v-model="openDialog" title="distribucin" @closed="openDialog = false">
        <template #header>
          <h4 class="not-margin">
            Desea guardar la <b>distribucin</b> de las aulas?
          </h4>
        </template>
        <div class=" flex  justify-end " >
          <vs-button transparent @click="openDialog = false" class="mr-2">Cancelar</vs-button>
          <vs-button @click="InsertarAsiento" class="ml-2">Aceptar</vs-button>
        </div>
      </vs-dialog>
    </div>

  </div>
</template>

<style scoped lang="scss">


</style>